.div-header{
  border-bottom: 4px solid rgb(218, 62, 62);
  display: flex;
  align-items: center;
  color: rgb(174, 39, 39);

}

.App-header {
  background-color: #f7f8fb;
  flex-direction: column;
  font-size: calc(10px + 2vmin);
  color: rgb(174, 39, 39);
  width: 270px;
  height: auto;
  margin-right: 10px;
}

label,input{
  margin-left: 10px;
  margin-bottom: 10px;
}
button{
  margin-left: 15px;
}

.App-body{
  margin-left: 40px;
  margin-top: 40px;
}
.div-footer{
  border-top: 4px solid rgb(218, 62, 62);
  margin-top: 500px;

}

.App-footer{
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
  font-family: Helvetica, Arial, sans-serif;
}
